/* Entire Document */
html, body {
  height: 100%;
  background: black; 
  /*scroll-behavior: smooth;*/
}


[ng\:cloak], [ng-cloak], .ng-cloak {
  display: none !important;
}

body {
  margin: 0;
  padding: 0;
  font-size: 14px;
  font-family: 'Roboto', sans-serif;
  z-index: 0;
  background: black;
  overflow-y: scroll;
}
button:focus {
  outline: 0;
}
#backBtn{
  left: 35px;
}
#nextBtn{
  right: 45px;
}
#backBtn i{
  padding-right: 3px;
}
#nextBtn i{
  padding-left: 3px;
}
.sliderBtn i{
  color: rgb(188, 188, 188);
  width: 52px;
  height: 52px;
  font-size: 30px;
}
.sliderBtn i:hover{
  color: rgb(255, 255, 255);
}
.sliderBtn{
  position: absolute;
  bottom: 0px;
  width: 52px;
  height: 52px;
  border-radius: 50%;
 /* border: 3px solid black;
  */
  border: none;
  background: rgba(207, 208, 209, 0);
}
/* .sliderBtn:hover{
  background: rgb(113, 113, 113);
}
*/
/* !Overlay! */

#overlay{
  position: fixed; 
  z-index: 3; 
  bottom: 10px;
  right: 10px;
  width: auto;
  height: auto; 
  background: rgba(0, 0, 0, 0.8); 
  color: white; 
  border-radius: 15px; 
  text-align: center; 
  padding: 5px; 
  max-width: 30%;
}

#overlayContainer{
  position: relative; 
  color: white; 
}

.previewUL{
  padding: 5px;
  margin: 0px;
}

.previewUL #overlayFrontEnd li,  .previewUL #overlayBackEnd li{
  border: 1px solid white; 
  max-width: 90%;
}

.shownOverlay{
  display: block;
}

.hiddenOverlay{
 /* display: none;  */
}

.toggleOverlayButton{
  position: absolute;
  right: 10px; 
  background: transparent;
  border: 2px solid white; 
  border-radius: 2px; 
  top: 5px;
}

.toggleOverlayButton:hover{
  cursor: pointer;
}

#toggleOverlayButtonIcon{
  color: white; 
}

/* !Nav! */
nav {
  background-color: #3B3738;
  margin: 0;
  overflow: hidden;
  color: white;
  border-bottom-color: #C9C9C9;
  border-bottom: solid 5px;;
  height: 80px;
  width: 100%;
  top: 0;
  z-index: 99;
  position: fixed;
/*  display: none; */
  display: block;  
}
/* Leftside of Nav */
#nic {
  font-size: 16px;
  font-weight: bold;
}

#navProfileImage {
  margin-right:10px;
  width: 66px;
  height: 66px;
  border-radius: 50px;
}

#nameNavContainer{
  display: flex;
  flex-direction: row;
  height: 70px;
  padding: 8px 0;
}

#nameNavContainerText{
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#nicJob {
  font-style: italic;
  margin-top: -15px;
}
/* Nav Positioning */
nav ul {
  margin: 0;
  padding: 0;
  display: flex;
}
nav ul li {
  display: inline-block;
  list-style-type: none;
}
nav ul li a {
  color: white;
  background-color: #521A19;
  display: block;
  height: 100%;
  padding: 20px 20px;
  text-decoration: none;
}
nav ul li:first-child {
  margin-right: auto;
}
#nameNavContainer {
  padding-left: 15px;
}
/* Right links */
nav ul li a:hover {
  background-color: #B20D0C;
}



.navIcon{
  font-size: 36px; 
}
/* About me Section */
#overlayExit{
  display: inline; 
  float: right; 
  font-size: 30px; 
}

#aboutMeButton, #logoCredits{
  color: #CDCCFF;
  cursor:pointer;
  background:none!important;
  border:none;
  padding:0!important;
  font-size: 14px;
  margin-left: 10px;
  display: inline; 
  font-style: normal; 
  font-weight: bold; 
}
#aboutMeButton:hover, #logoCredits:hover{
  text-decoration:underline;
}
#logoCredits{
  position: absolute;
  left: 0xp;
}
#aboutMeText, #credit{
  position: absolute;
  top: 50%;
  left: 50%;
  font-size: 20px;
  color: white;
  transform: translate(-50%,-50%);
  -ms-transform: translate(-50%,-50%);
  width: 90%;
}
/* !Projects! */
#projects{
  margin: 0 auto;
  width: 100%;
  position: relative;
  height: 90%;
}
#projectInfoText{
  position: absolute;
  bottom: 0px;
  color: white;
  background: rgb(0, 0, 0);
  background: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 100%;
}
#content{
  position: relative;
 /* max-width: 700px;
  */
  margin: 0 auto;
  width: 100%;
  height: 100%;
}

#video-controls{
  z-index: 3;
  position: absolute; 
  top: 20px;
  left: 50%;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%)
}
.video-control-button{
  font-size: 34px;
}
.video-control-button:hover{
  cursor: pointer;
  color: rgb(180, 180, 180);
}


#projectTitle{
 /* padding-top: 4%;
  */
}
#topLeftContent{
  position: absolute;
  left: 10px;
}
#projectAdditionalInformation{
 /*display: none;*/
  max-width: 40%;
  background: rgba(0, 0, 0, 0.8);
  padding: 5px;
  border-radius: 15px;
}
#toggleButton{
  background: rgb(42, 42, 42);
  border: 3px solid black;
  color: rgb(255, 255, 255);
  padding: 15px;
  font-size: 16px;
  border-radius: 10px;
  margin: 10px auto; 
  border-color: white;
}
#toggleOverlayButtonClosed{
  z-index: 30;
}
.previewLogo{
  float: left;
  height: 50px;
  width: 50px;
}
